import React, { useEffect, useState } from "react";
import { Button, Modal, Form } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { Switch } from "react-router-dom";
import {
  resourceapprovedlist,
  resourceupdatelist,
} from "../../reduxx/actions/actions";

function ApprovedResources() {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(resourceapprovedlist({ status: "approved" }));
  }, []);
  var pendinglist = useSelector(
    ({ resourceapprovedlist }) => resourceapprovedlist.payload
  );
  console.log("pending", pendinglist);
  const [show, setShow] = useState(false);
  const [id, setId] = useState("");
  const [reason, setReason] = useState();
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const handleClick = () => {
    dispatch(
      resourceupdatelist({ id: id, status: "rejected", review: reason })
    );
    handleClose();
  };
  return (
    <div className="row">
      <div className="col-12 grid-margin">
        <div className="card">
          <div className="card-body">
            <h4 className="card-title"> Approved List </h4>
            <div className="table-responsive">
              <table className="table">
                <thead>
                  <tr>
                    <th> Name </th>
                    <th> Mobile </th>
                    <th> City </th>
                    <th> Reason </th>
                    <th> Resource Type </th>
                    <th>Status</th>
                    <th> Actions </th>
                  </tr>
                </thead>
                <tbody>
                  {Array.isArray(pendinglist) && pendinglist.length > 0 ? (
                    pendinglist.map((item) => {
                      return (
                        <tr>
                          <td>
                            <img
                              src={item.photos}
                              className="mr-2"
                              alt="face"
                            />
                            {item.name}
                          </td>
                          <td> {item.mobile} </td>
                          <td> {item.city} </td>
                          <td> {item.reason} </td>
                          <td> {item.resource_type} </td>
                          <td> {item.status} </td>
                          <td>
                            <select
                              class="custom-select"
                              onChange={(event) => {
                                switch (event.target.value) {
                                  case "pending":
                                    dispatch(
                                      resourceupdatelist({
                                        id: item.id + "",
                                        status: "pending",
                                      })
                                    );
                                    break;
                                  case "rejected":
                                    setId(item.id + "");
                                    handleShow();
                                  default:
                                    break;
                                }
                                event.target.value = "update_status";
                              }}
                            >
                              <option value="update_status">
                                Update Status
                              </option>
                              <option value="pending">Pending</option>
                              <option value="rejected">Reject</option>
                            </select>
                          </td>
                        </tr>
                      );
                    })
                  ) : (
                    <tr>
                      <td colspan="7" style={{ textAlign: "center" }}>
                        No Data Found
                      </td>
                    </tr>
                  )}
                </tbody>

                <Modal show={show} onHide={handleClose}>
                  <Modal.Header closeButton style={{ backgroundColor: "#fff" }}>
                    <Modal.Title>Reason</Modal.Title>
                  </Modal.Header>
                  <Modal.Body
                    style={{ backgroundColor: "#fff", color: "black" }}
                  >
                    <Form.Control
                      type="text"
                      name="reason"
                      placeholder="Enter reason"
                      onChange={(e) => setReason(e.target.value)}
                      aria-describedby="passwordHelpBlock"
                    />
                  </Modal.Body>
                  <Modal.Footer
                    style={{ backgroundColor: "#fff", color: "black" }}
                  >
                    <Button variant="danger" onClick={handleClick}>
                      Reject
                    </Button>
                  </Modal.Footer>
                </Modal>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ApprovedResources;
